import React from "react";
import { Select } from "antd";

export default function SystemEventTypeSelector(props) {
    return (
        <Select {...props}>
            <Select.Option value="0">Create</Select.Option>
            <Select.Option value="1">Delete</Select.Option>
            <Select.Option value="2">Modify</Select.Option>
        </Select>
    );
}
